import React, { Component } from 'react';
import './Scroll.styl';

class Scroll extends Component {

	componentDidMount() {
		const scroll = document.querySelector('.scroll'),
			  height = window.innerHeight;

		window.addEventListener('scroll', (e) => {
			if (window.pageYOffset > height/2) {
				scroll.classList.remove('scroll-hidden')
			} else {
				if (!scroll.classList.contains('scroll-hidden')) {
					scroll.classList.add('scroll-hidden');
				}
			}
		});
	};

	static componentWillUnmount() {
		window.removeEventListener('scroll');
	}

	clickToScroll = () => {
		const height = window.innerHeight;

		let interval = setInterval(()=> {

			if (window.pageYOffset > 0) {
				window.scrollTo(0,window.pageYOffset - height/5);
			} else {
				clearInterval(interval);
			}

		}, 1);
	};

    render() {
        return (
			<div className="scroll scroll-hidden" onClick={this.clickToScroll}>
				<div className="scroll--arrow-container">
				</div>
			</div>
		);
    };
}

export default Scroll;


