import React, { Component } from "react";
import "./Aside.styl";
import { Card, ModalBox, ModalHeader } from "components/UI";
import { random } from 'components/features';

import maintainsPicture from 'img/maintains.jpg';
import sceneryPicture1 from 'img/scenery1.jpg';
import reactPicture from 'img/react.png';

import { ModalContainer } from "../ModalBox/ModalBox";

class Aside extends Component {

	openModal = (e) => {
		let targetData = e.target.getAttribute("data-target"),
			targetElement = document.querySelector("#"+targetData);

		targetElement.classList.add("modal-box--opened");

		document.querySelector('body').style.overflowY = 'hidden';
	};

	render() {
		return (
			<div className="aside-right">
				<Card>
					<h3 className="h3--emphasised">{random.text(3,5)}</h3>
					<img src={maintainsPicture} alt=""/>
					<p>{random.text(5,10)}</p>
				</Card>

				<Card>
					<h3 className="h3--emphasised">{random.text(4,5)}</h3>
					<img src={sceneryPicture1} alt=""/>
					<p>{random.text(15)}</p>
				</Card>

				<Card>
					<h3 className="h3--emphasised">{random.text(1,3)}</h3>
					<p>{random.text(5,15)}</p>
					<button type="button" onClick={this.openModal} data-target="aside-modal-ad" className="button">{random.text(1,2)}</button>
					<ModalBox id="aside-modal-ad">
						<ModalHeader>
							<p>{random.text(2,4)}</p>
						</ModalHeader>
						<ModalContainer>
							<img src={reactPicture} alt=""/>
							<p>{random.text(100,300)}</p>
						</ModalContainer>
					</ModalBox>
				</Card>
			</div>
		);
	};

}
export default Aside;


