import React, { Component } from 'react';
import { Link } from 'react-router';
import * as links from 'links';
import { random } from 'components/features';
import { MobileMenuButton } from '../../UI';

import './Header.styl';

class Header extends Component {
    render() {
        return (
        	<header className="header">
				<div className="row">
					<MobileMenuButton/>

					<Link className="header--text-logo" to={links.index}>
							Site
					</Link>

					<p className="header--text float-right">
						{random.text(3,5)}
					</p>
				</div>
			</header>
		);
    };
}

export default Header;


