import React, { Component } from 'react';
import './MobuleMenuButton.styl';

class MobileMenuButton extends Component {
	onclick = (e) => {
		if (!document.querySelector('.aside').classList.contains('aside--opened')) {
			document.querySelector('.aside').classList.add('aside--opened');
			document.querySelector('body').style.overflowY = 'hidden';
		} else {
			document.querySelector('.aside').classList.remove('aside--opened');
			document.querySelector('body').style.overflowY = 'auto';
		}
	};

    render() {
        return (
			<div className="mobile-menu" onClick={this.onclick}>

			</div>
		);
    };
}

export default MobileMenuButton;


