import Chance  from 'chance';
const chance = new Chance();

let random = {
	text: (min, max) => {
		if (min && max) {
			return chance.sentence({
				words: chance.natural({ min: min, max: max })
			});
		} else {
			return chance.sentence({
				words: min
			});
		}
	},
	phone: () => {
		return chance.phone();
	}

};

export default random;


