import React, { Component } from 'react';
import './ModalBox.styl';

class ModalHeader extends Component {
	render() {
		return (
			<div className="modal--header">
				{this.props.children}
			</div>
		);
	};

}

class ModalContainer extends Component {
	render() {
		return (
			<div className="modal--container">
				{this.props.children}
			</div>
		);
	};
}

class ModalBox extends Component {
	closeModal = (e) => {
		let target = e.target;

		if (target.classList.contains("modal-box") && !target.classList.contains("modal-wrapper")) {
			e.target.classList.remove("modal-box--opened");
			e.target.classList.remove("modal-box__lighten");
			document.querySelector('body').style.overflowY = 'auto';
		}
	};

	closeModalByKeyPress = () => {
		document.querySelector('.modal-box').classList.remove("modal-box--opened");
		document.querySelector('.modal-box').classList.remove("modal-box__lighten");
		document.querySelector('body').style.overflowY = 'auto';
	};

	mouseHandle = (e) => {
		let target = e.target,
			modalBox = document.querySelector('.modal-box');

		if (target.classList.contains("modal-box") && !target.classList.contains("modal-wrapper")) {
			modalBox.classList.add("modal-box__lighten");
		} else {
			modalBox.classList.remove("modal-box__lighten");
		}
	};

	componentDidMount() {
		document.addEventListener('keydown', (e) => {
			if (e.keyCode === 27) {
				this.closeModalByKeyPress();
			}
		})
	}

	componentWillUnmount() {
		document.removeEventListener('keydown');
	}

    render() {
        return (
        	<div className="modal-box" onMouseOver={this.mouseHandle} onClick={this.closeModal} {...this.props}>
				<div className="modal-wrapper">
					{this.props.children}
				</div>
			</div>
		);
    };
}

export {
	ModalBox,
	ModalHeader,
	ModalContainer
};


