import React from 'react';
import './Footer.styl';
import { Link } from 'react-router';
import { random } from 'components/features';

const Footer = () => {
    return (
    	<footer className="footer">
			<div className="row">
				<div className="footer--col1">
					<ul className="footer--links">
						<li><Link activeClassName="active" className="footer--link-item" to="about">About</Link></li>
						<li><Link activeClassName="active" className="footer--link-item" to="contacts">Contact Us</Link></li>
						<li><Link activeClassName="active" className="footer--link-item" to="link1">{random.text(1,3)}</Link></li>
						<li><Link activeClassName="active" className="footer--link-item" to="link2">{random.text(1,3)}</Link></li>
					</ul>
					<ul className="footer--links">
						<li><Link activeClassName="active" className="footer--link-item" to="link3">{random.text(1,3)}</Link></li>
						<li><Link activeClassName="active" className="footer--link-item" to="link4">{random.text(1,3)}</Link></li>
						<li><Link activeClassName="active" className="footer--link-item" to="link5">{random.text(1,3)}</Link></li>
						<li><Link activeClassName="active" className="footer--link-item" to="link6">{random.text(1,3)}</Link></li>
					</ul>
				</div>
				<div className="footer--col2">
					&copy; {random.text(2)}
				</div>
			</div>
		</footer>
	);
};

export default Footer;


