import React from 'react';
import './About.styl';
import {random} from 'components/features';

import man from 'img/man.png';
import woman from 'img/woman.png';
import smilingMan from 'img/smiling-man.png';

const About = () => {
    return (
        <div className="about--container">
			<div className="about--list">
				<div className="about--list-item">
					<h3>{random.text(2)}</h3>

					<img src={man} alt=""/>

					<p>{random.text(100,300)}</p>
				</div>

				<div className="about--list-item">
					<h3>{random.text(2)}</h3>

					<img src={woman} alt=""/>

					<p>{random.text(100,300)}</p>
				</div>

				<div className="about--list-item">
					<h3>{random.text(2)}</h3>

					<img src={smilingMan} alt=""/>

					<p>{random.text(100,300)}</p>
				</div>
			</div>
		</div>
    );
};

export default About;


