import React, { Component } from 'react';
import './Main.styl';
import {random} from 'components/features';
import foxPicture from 'img/fox.jpg';
import elephantPicture from 'img/elephant.jpg';

class Main extends Component {
    render() {
        return (
        	<div className="main">
				<article className="article">
					<h1>{random.text(3,5)}</h1>
					<div data-name={random.text(2)} className="img-wrapper">
						<img src={foxPicture} alt=""/>
					</div>
					<p>
						{random.text(500,1000)}
					</p>
				</article>
				<article className="article">
					<h1>{random.text(3,5)}</h1>
					<div data-name={random.text(2)} className="img-wrapper">
						<img src={elephantPicture} alt=""/>
					</div>
					<p>
						{random.text(500,1000)}
					</p>
				</article>
			</div>
		);
    };
}

export default Main;


