import React from 'react';
import ReactDOM from 'react-dom';
import App from './components/App';

import { Router, Route, IndexRoute, hashHistory } from 'react-router';
import * as links from './links';

import { Index, About, Error404, Contacts } from './components/Pages';

import './index.styl';

function changePage() {
	window.scrollTo(0,0);

	let change = document.querySelectorAll('.close-on-change:checked'),
		menuOpened = document.querySelector('aside').classList.contains('aside--opened') || false;

	if (change) {
		[].forEach.call(change, (e) => {
			e.checked = false;
		});
	}

	if (menuOpened) {
		document.querySelector('aside').classList.remove('aside--opened');
		document.querySelector('body').style.overflowY = 'auto';
	}
}

ReactDOM.render(
	<Router onUpdate={changePage} history={hashHistory}>
		<Route path={links.index} component={App}>
			<IndexRoute component={Index}/>
			<Route path={links.about} component={About}/>
			<Route path={links.contacts} component={Contacts}/>
			<Route path="*" component={Error404}/>
		</Route>
	</Router> ,
  document.getElementById('root')
);



